<template>
  <div class="catalog">
    <h1>Каталог</h1>
    <form class="form">
      <input class="input" v-model="newINN" type="text" placeholder="ИНН">
      <input class="input" v-model="newName" type="text" placeholder="Название">
      <input class="input" v-model="newCEO" type="text" placeholder="Руководитель">
      <button class="btn btn--form" @click.prevent="addItem">Добавить</button>
    </form>
    <table class="table" v-if="companies.length !=0">
      <thead>
        <tr>
          <th class="mobile-hidden">№</th>
          <th @click="sortByINN()">ИНН</th>
          <th @click="sortByName()">Наименование</th>
          <th @click="sortByCEO()">Руководитель</th>
          <th class="mobile-hidden">Управление</th>
        </tr>
      </thead>
      <tbody>
        <Pagination :list-data="companies" @edit="edit" @remove="remove"/>
      </tbody>
    </table>
  </div>
</template>

<script>
import Pagination from "./Pagination.vue";

export default {
  name: "Catalog",
  components: {
    Pagination
  },
  data() {
    return {
      // если в хранилище пусто, то пустой массив
      companies: JSON.parse(localStorage.getItem("array")) || [],
      newINN: "",
      newName: "",
      newCEO: "",
      sort: {
        // технический объект для реализации сортировки. По-умолчанию по первому клику сортировка будет по возрастанию.
        INN: "asc",
        name: "asc",
        CEO: "asc"
      }
    };
  },
  methods: {
    addItem() {
      if (this.newINN && this.newName && this.newCEO) {
        // проверка на заполнение всех полей
        this.companies.push({
          INN: this.newINN,
          name: this.newName,
          CEO: this.newCEO,
          isEditing: false,
          id: this.companies.length + 1
        });
        this.newINN = "";
        this.newName = "";
        this.newCEO = "";
        try {
          var serialObj = JSON.stringify(this.companies); //сериализуем его
          localStorage.setItem("array", serialObj);
        } catch (error) {
          alert("Закончилось место в хранилище. Запись не будет сохранена.");
        }
      } else {
        alert("Введите все данные");
      }
    },
    remove(index) {
      this.companies.splice(index, 1);
      // обновляем localStorage
      try {
        var serialObj = JSON.stringify(this.companies); //сериализуем его
        localStorage.setItem("array", serialObj);
      } catch (error) {
        alert("Закончилось место в хранилище. Запись не будет сохранена.");
      }
    },
    edit(item) {
      // если в записи isEditing === true, показываем инпуты, скрываем <td>
      item.isEditing = !item.isEditing;
      // обновляем localStorage
      try {
        var serialObj = JSON.stringify(this.companies); //сериализуем его
        localStorage.setItem("array", serialObj);
      } catch (error) {
        alert("Закончилось место в хранилище. Запись не будет сохранена.");
      }
    },
    sortByINN() {
      if (this.sort.INN === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.INN - b.INN;
        });
        this.sort.INN = "desc"; // следующая сортировка будет наоборот
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.INN - a.INN;
        });
        this.sort.INN = "asc";
      }
    },
    sortByName() {
      if (this.sort.name === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.name.localeCompare(b.name);
        });
        this.sort.name = "desc";
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.name.localeCompare(a.name);
        });
        this.sort.name = "asc";
      }
    },
    sortByCEO() {
      if (this.sort.CEO === "asc") {
        this.companies = this.companies.sort((a, b) => {
          return a.CEO.localeCompare(b.CEO);
        });
        this.sort.CEO = "desc";
      } else {
        this.companies = this.companies.sort((a, b) => {
          return b.CEO.localeCompare(a.CEO);
        });
        this.sort.CEO = "asc";
      }
    }
  }
};
</script>

